const components = {}
const files = require.context('./', true, /index.vue$/)
files.keys().forEach(item => {
  const route = item.replace(/\.\/|\/index\.vue/g, '').split('/')
  const key = route
    .map(mapItem =>
      mapItem.replace(/^\S|\-./g, str => str.toUpperCase()).replace('-', '')
    )
    .join('')
  const value = files(item).default
  components[key] = value
})
export default components
