<template>
  <a
    :style="{
      ...dataSource.style
    }"
    type="link"
    href="javascript:;"
    class="link"
    @click="onClick"
  >
    <span>
      {{ dataSource.option.label }}
    </span>
  </a>
</template>

<script>
import { dispatchEvent } from '@/utils/methods'
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  methods: {
    onClick() {
      const {
        option: { url, blank }
      } = this.dataSource
      dispatchEvent('beforeRouteLeave', {
        callback: () => {
          window.open(
            url.includes('http') ? url : location.origin + url,
            blank ? '_blank' : '_self'
          )
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: rgba(255, 255, 255, 0.85);
}
</style>
