<template>
  <div
    class="shape"
    :class="`shape-${dataSource.data.shape}`"
    :style="style"
  ></div>
</template>
<script>
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  computed: {
    style() {
      const {
        layout: { width, height },
        style
      } = this.dataSource
      const { backgroundGradient, backgroundColor } = style
      return {
        ...style,
        width: (width || 100) + 'px',
        height: (height || 100) + 'px',
        backgroundColor:
          backgroundGradient?.show && backgroundGradient?.color.length
            ? undefined
            : backgroundColor || '#1484c7'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.shape {
  width: 100%;
  height: 100%;
}
.shape-circle {
  border-radius: 100%;
}
</style>
