<template>
  <div
    :style="{
      ...dataSource.style
    }"
    class="video-wrapper"
  >
    <video
      v-if="dataSource.data.url"
      ref="video"
      class="video"
      :src="dataSource.data.url"
      :width="dataSource.layout.width"
      :height="dataSource.layout.height"
      :autoplay="dataSource.option.autoplay"
      :loop="dataSource.option.loop"
      :muted="dataSource.option.muted"
      :controls="dataSource.option.controls"
    >
      您的浏览器不支持 video 视屏播放。
    </video>
    <img v-else :src="require('./images/thumbnail.png')" alt="placeholder" />
  </div>
</template>

<script>
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  watch: {
    'dataSource.option.autoplay': function(newVal) {
      if (newVal) {
        this.$refs.video.play()
      } else {
        this.$refs.video.pause()
      }
    },
    'dataSource.option.loop': function(newVal) {
      if (newVal) this.$refs.video.play()
    }
  }
}
</script>

<style lang="less" scoped>
.video-wrapper {
  width: 100%;
  height: 100%;
  & > img {
    width: 100%;
    height: 100%;
  }
  .video {
    object-fit: fill;
  }
}
</style>
