<template>
  <div
    class="chart"
    :style="{
      ...dataSource.style
    }"
  ></div>
</template>

<script>
export default {
  name: 'Map',
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      chart: undefined
    }
  },
  computed: {
    option() {
      return {
        backgroundColor: 'transparent',
        ...this.dataSource.data,
        ...this.dataSource.option
      }
    }
  },
  watch: {
    'dataSource.layout.width': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.chart.resize()
      }
    },
    'dataSource.layout.height': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.chart.resize()
      }
    },
    'dataSource.data': {
      handler: function(newVal, oldVal) {
        if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
          this.setOption(true)
        }
      },
      deep: true
    },
    'dataSource.option': {
      handler: function(newVal, oldVal) {
        if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
          this.setOption(true)
        }
      },
      deep: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart()
    })
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el)
      this.setOption()
    },
    setOption(notMerge = false) {
      this.chart.setOption({ ...this.option }, notMerge)
    }
  }
}
</script>

<style lang="less" scoped>
.chart {
  width: 100%;
  height: 100%;
}

rect {
  fill: transparent !important;
}
</style>
