<template>
  <div class="tabs-wrapper">
    <ul
      :style="{ flexDirection: (layout && layout.direction) || 'row' }"
      class="tabs"
    >
      <li
        v-for="(item, index) of dataSource.option.series"
        :key="index"
        :class="{ active: activeIndex === index }"
        :style="{
          ...itemStyle,
          ...emphasisItemStyle(activeIndex === index)
        }"
        @click="onClick(index)"
      >
        <span
          :style="{ ...textStyle, ...emphasisTextStyle(activeIndex === index) }"
        >
          {{ item.name }}
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    },
    play: {
      type: Boolean,
      default: () => false
    }
  },
  data() {
    return {
      activeIndex: 0
    }
  },
  computed: {
    layout() {
      return this.dataSource.option.layout
    },
    itemStyle() {
      const {
        width,
        height,
        borderWidth,
        borderRadius,
        shadowOffsetY,
        shadowOffsetX,
        shadowBlur,
        shadowSpread,
        shadowColor,
        shadowInset
      } = this.dataSource.option.itemStyle
      return {
        ...this.dataSource.option.itemStyle,
        width: width ? `${width}px` : 'auto',
        height: height ? `${height}px` : 'auto',
        flex: width || height ? 'none' : 1,
        borderWidth: `${borderWidth || 0}px`,
        borderRadius: `${borderRadius}px`,
        boxShadow: `${shadowOffsetY || 0}px
          ${shadowOffsetX || 0}px
          ${shadowBlur || 0}px
          ${shadowSpread || 0}px
          ${shadowColor}
          ${shadowInset === 'inset' ? 'inset' : ''}`
      }
    },
    textStyle() {
      const { fontSize } = this.dataSource.option.textStyle
      return {
        ...this.dataSource.option.textStyle,
        fontSize: `${fontSize}px`
      }
    },
    emphasisItemStyle() {
      const {
        borderWidth,
        borderRadius,
        shadowOffsetY,
        shadowOffsetX,
        shadowBlur,
        shadowSpread,
        shadowColor,
        shadowInset
      } = this.dataSource.option.emphasis.itemStyle
      return isActive => {
        if (isActive) {
          return {
            ...this.dataSource.option.emphasis.itemStyle,
            borderWidth: `${borderWidth || 0}px`,
            borderRadius: `${borderRadius}px`,
            boxShadow: `${shadowOffsetY || 0}px
          ${shadowOffsetX || 0}px
          ${shadowBlur || 0}px
          ${shadowSpread || 0}px
          ${shadowColor}
          ${shadowInset === 'inset' ? 'inset' : ''}`
          }
        } else {
          return {}
        }
      }
    },
    emphasisTextStyle() {
      const { fontSize } = this.dataSource.option.emphasis.textStyle
      return isActive => {
        if (isActive) {
          return {
            ...this.dataSource.option.emphasis.textStyle,
            fontSize: `${fontSize ||
              this.dataSource.option.textStyle.fontSize}px`
          }
        } else {
          return {}
        }
      }
    }
  },
  mounted() {
    if (this.play) {
      this.carousel()
    }
  },
  methods: {
    togglePanel() {
      for (const item of this.dataSource.option.series) {
        if (item.bind.id) {
          document.querySelector(`#${item.bind.id}`).style.opacity = 0
        }
      }
      const active = this.dataSource.option.series[this.activeIndex]
      if (active.bind.id) {
        document.querySelector(`#${active.bind.id}`).style.opacity = 1
      }
    },
    carousel() {
      const { animation = {}, series } = this.dataSource.option
      const { interval } = animation
      this.togglePanel()
      if (interval) {
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.activeIndex =
            this.activeIndex === series.length - 1 ? 0 : this.activeIndex + 1
          this.carousel()
        }, interval)
      }
    },
    onClick(acitveIndex) {
      clearTimeout(this.timer)
      this.activeIndex = acitveIndex
      this.togglePanel()
      setTimeout(() => {
        this.carousel()
      }, 2000)
    }
  }
}
</script>

<style lang="less" scoped>
.tabs-wrapper {
  width: 100%;
  height: 100%;
  .tabs {
    display: flex;
    justify-content: space-between;
    height: 100%;
    padding: 0;
    margin: 0;
    list-style: none;

    & > li {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      transition: all 0.3s;
    }

    .active {
      background-color: #0a73ff;
    }
  }
}
</style>
