<template>
  <div
    class="clock"
    :style="{
      ...dataSource.style
    }"
  >
    <span>{{ formatedDateTime }}</span>
  </div>
</template>
<script>
import moment from 'moment'
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      dateTime: moment()
    }
  },
  computed: {
    formatedDateTime() {
      const { format } = this.dataSource.option
      return this.dateTime && this.dateTime.format(format)
    }
  },
  watch: {
    'dataSource.option.format': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        clearTimeout(this.timer)
        if (newVal.includes('s')) {
          this.ticktock(1000)
        } else if (newVal.includes('m')) {
          this.ticktock(1000 * 60)
        } else if (newVal.includes('h')) {
          this.ticktock(1000 * 60 * 60)
        } else {
          this.ticktock(1000 * 60 * 60 * 12)
        }
      }
    }
  },
  mounted() {
    moment.locale('zh-cn')
    this.ticktock()
  },
  beforeDestroy() {
    clearTimeout(this.timer)
  },
  methods: {
    ticktock(delay = 1000) {
      this.dateTime = moment()
      this.timer = setTimeout(() => {
        this.ticktock(delay)
      }, delay)
    }
  }
}
</script>
<style lang="less" scoped>
.clock {
  height: 19px;
}
</style>
