import Vue from 'vue'
/**
 * @description 绑定事件 bindEvent(element, event, handler)
 */
export const bindEvent = (function() {
  if (document.addEventListener) {
    return function(element, event, handler) {
      if (element && event && handler) {
        element.addEventListener(event, handler, false)
      }
    }
  } else {
    return function(element, event, handler) {
      if (element && event && handler) {
        element.attachEvent('bindEvent' + event, handler)
      }
    }
  }
})()
/**
 * @description 解绑事件 removeEvent(element, event, handler)
 */
export const removeEvent = (function() {
  if (document.removeEventListener) {
    return function(element, event, handler) {
      if (element && event) {
        element.removeEventListener(event, handler, false)
      }
    }
  } else {
    return function(element, event, handler) {
      if (element && event) {
        element.detachEvent('bindEvent' + event, handler)
      }
    }
  }
})()
/**
 * @description 防抖
 */
export const debounce = (fn, delay) => {
  let timer
  return () => {
    clearTimeout(timer)
    timer = setTimeout(() => {
      fn()
    }, delay)
  }
}
/**
 * @description 复制内容到剪贴板
 * @param {String} text
 */
export function copyTextToClipboard(text) {
  const textArea = document.createElement('textarea')

  textArea.style.position = 'fixed'
  textArea.style.top = 0
  textArea.style.left = 0
  textArea.style.width = '2em'
  textArea.style.height = '2em'
  textArea.style.padding = 0
  textArea.style.border = 'none'
  textArea.style.outline = 'none'
  textArea.style.boxShadow = 'none'
  textArea.style.background = 'transparent'
  textArea.value = text

  document.body.appendChild(textArea)
  textArea.select()

  let result

  try {
    result = document.execCommand('copy')
    Vue.prototype.$message.success('复制成功')
  } catch (err) {
    result = false
    Vue.prototype.$message.error('复制失败')
  }

  document.body.removeChild(textArea)

  return result
}
/**
 * @description 千分位显示
 * @param {Number} num
 */
export const toThousands = num => {
  num = num !== undefined ? num : ''
  return num
    .toString()
    .replace(/,/g, '')
    .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
}
/**
 * @description 登录
 */
export const login = () => {
  const origin = encodeURIComponent(location.origin)
  location.href = `/api/v1/login?forward=${origin}`
}
/**
 * @description 登出
 */
export const logout = () => {
  const redirect = encodeURIComponent(location.href)
  location.href = `/api/v1/logout?forward=${redirect}`
}
/**
 * @description 生成随机数
 */
export const randomNum = () => {
  return Math.random()
    .toString(36)
    .substr(2)
}
/**
 * @description 深度克隆
 */
function isType(value, type) {
  const dataType = Object.prototype.toString.call(value)
  switch (type) {
    case 'Object':
      return dataType === '[object Object]'
    case 'Array':
      return dataType === '[object Array]'
    default:
      return false
  }
}
export const deepClone = obj => {
  let newObj
  if (isType(obj, 'Object')) {
    newObj = {}
    for (let key in obj) {
      newObj[key] = deepClone(obj[key])
    }
  } else if (isType(obj, 'Array')) {
    newObj = []
    for (let item of obj) {
      newObj.push(deepClone(item))
    }
  } else {
    newObj = obj
  }
  return newObj
}
/**
 * @description 转成罗马数字
 */
const covertToRoman = num => {
  let newNum = ''
  const o = num % 10
  const t = Math.floor((num % 100) / 10)
  const h = Math.floor((num % 1000) / 100)
  const k = Math.floor(num / 1000)
  const one = ['I', 'II', 'III', 'IV', 'V', 'VI', 'VII', 'VIII', 'IX']
  const ten = ['X', 'XX', 'XXX', 'XL', 'L', 'LX', 'LXX', 'LXXX', 'XC']
  const hundred = ['C', 'CC', 'CCC', 'CD', 'D', 'DC', 'DCC', 'DCCC', 'CM']
  const thousand = 'M'
  for (let i = 0; i < k; i++) {
    newNum += thousand
  }
  if (h) newNum += hundred[h - 1]
  if (t) newNum += ten[t - 1]
  if (o) newNum += one[o - 1]
  return newNum
}
/**
 * @description 转成汉字数字
 */
export const convertToChinaNum = num => {
  const cnNum = ['零', '一', '二', '三', '四', '五', '六', '七', '八', '九']
  const unit = ['', '十', '百', '千', '万']
  num = parseInt(num)
  const getWan = temp => {
    let strArr = temp
      .toString()
      .split('')
      .reverse()
    let newNum = ''
    for (let i = 0; i < strArr.length; i++) {
      newNum =
        (i == 0 && strArr[i] == 0
          ? ''
          : i > 0 && strArr[i] == 0 && strArr[i - 1] == 0
          ? ''
          : cnNum[strArr[i]] + (strArr[i] == 0 ? unit[0] : unit[i])) + newNum
    }
    return newNum
  }
  let noWan = num % 10000
  const overWan = Math.floor(num / 10000)
  if (noWan.toString().length < 4) {
    noWan = '0' + noWan
  }
  return overWan ? getWan(overWan) + '万' + getWan(noWan) : getWan(num)
}
/**
 * @description 格式化数字
 * 罗马数字、汉字数字
 */
export const formatNumber = (number, format) => {
  switch (format) {
    case 'roman':
      return covertToRoman(number)
    case 'cn':
      return convertToChinaNum(number)
    default:
      return number
  }
}
/**
 * @description 手动触发自定义事件
 */
export const dispatchEvent = (action, params = {}) => {
  const event = new CustomEvent(action, {
    detail: params
  })
  window.dispatchEvent(event)
}
/**
 * @description 坐标轴排序
 * @param {Array} data
 * @param {Object} option
 * @returns
 */
export const sortAxis = (data = [], option = {}) => {
  const { sort, name } = option.series.filter(item => item.sort)[0] || {}
  const axis = [...new Set(data.map(item => item.axis))]
  return axis
    .map(axisItem => {
      return data.filter(
        dataItem =>
          (name && dataItem.legend ? dataItem.legend === name : true) &&
          dataItem.axis === axisItem
      )[0]
    })
    .sort((a, b) => {
      if (sort === 'asc') {
        return a.value - b.value
      } else if (sort === 'desc') {
        return b.value - a.value
      }
    })
}

const estimate = (expression, source) => {
  let bool = true
  const splitedExp = expression.split(' ')
  const path = expression.startsWith('!')
    ? splitedExp[0].replace('!', '')
    : splitedExp[0]
  const value = evalValue(path, source)
  const symbol = splitedExp[1]
  if (symbol) {
    const compare = splitedExp[2]
    switch (symbol) {
      case '===':
        bool = value === compare
        break
      case '!==':
        bool = value !== compare
        break
      case '>':
        bool = value > compare
        break
      case '<':
        bool = value < compare
        break
    }
  } else if (expression.startsWith('!')) {
    bool = !value
  } else {
    bool = value
  }
  return bool
}
/**
 * @description 生成条件
 * @param {Array} condition
 * @param {Object} source
 * @returns
 */
export const generateCondition = (condition, source) => {
  const conditionType = Object.prototype.toString.call(condition)
  if (conditionType === '[object Array]' && condition.length) {
    const visible = []
    for (let i = 0; i < condition.length; i++) {
      visible.push(estimate(condition[i], source))
    }
    return visible.findIndex(item => item === true) !== -1
  }
  return true
}
/**
 * @description 生成对象
 * @param {String} key
 * @param {Object} source // 直接改变原始对象
 * @returns
 */
export const evalValue = (key = '', source) => {
  if (key) {
    const keyArr = key.split('.')
    const value = keyArr.reduce((acc = {}, cur, curIndex) => {
      if (acc[cur]) {
        return (acc[cur] = acc[cur])
      } else if (curIndex < keyArr.length - 1) {
        return Vue.set(acc, cur, {})
      }
    }, source)
    return value
  } else {
    return source
  }
}
/**
 * @description 递归搜索
 * @param {Array} arr
 * @param {String} key
 * @param {String} value
 * @param {Function} callback
 */
export const loopFind = (arr, id, callback) => {
  arr.forEach((item, index) => {
    if (item.id === id) {
      return callback(item, index, arr)
    }
    if (item.children) {
      return loopFind(item.children, id, callback)
    }
  })
}
/**
 * 判断是否为 undefined
 * @param {*} value
 * @returns
 */
export const isUndefined = value => {
  return typeof value === 'undefined'
}
/**
 * 运行字符串代码
 * @param {String} str
 */
export const runCode = str => {
  if (str) {
    return new Function('"use strict";return (' + str + ')')()
  }
}
/**
 * 禁止浏览器缩放功能
 */
export const forbiddenBrowserScale = callback => {
  const fn = e => {
    e.preventDefault()
    callback && callback(e)
  }
  window.addEventListener(
    // chrome
    'mousewheel',
    function(e) {
      if (e.ctrlKey === true || e.metaKey) {
        fn(e)
      }
    },
    { passive: false }
  )
  window.addEventListener(
    // Firefox
    'DOMMouseScroll',
    function(e) {
      if (e.ctrlKey === true || e.metaKey) {
        fn(e)
      }
    },
    { passive: false }
  )
  window.addEventListener('keydown', function(e) {
    if ((e.ctrlKey === true || e.metaKey) && ['+', '-'].indexOf(e.key) !== -1) {
      fn(e)
    }
  })
}
/**
 * 生成 css 渐变色格式
 */
export const generateBackgroundGradient = (gradient = {}) => {
  const { show, angle, color = [] } = gradient
  if (show && color.length > 1) {
    const linearGradient = color
      .map(item => `${item.color} ${item.offset * 100}%`)
      .join(',')
    return `linear-gradient(${angle || 0}deg, ${linearGradient})`
  }
}
