import { sortAxis, runCode } from '@/utils/methods'

export default function({ data = [], option = {} }) {
  if (Array.isArray(data)) {
    let mappedData = data?.map(item => {
      let legend = option?.fieldMaps?.legend || 'legend'
      let axis = option?.fieldMaps?.axis || 'axis'
      let value = option?.fieldMaps?.value || 'value'
      return {
        legend: String(item[legend]),
        axis: item[axis],
        value: item[value]
      }
    })
    const sortedAxis = sortAxis(mappedData, option)
    option.xAxis.data = sortedAxis.map(sortItem => sortItem.axis)
    option.yAxis.data = sortedAxis.map(sortItem => sortItem.axis)
    option.legend.data = [...new Set(mappedData.map(item => item.legend))]
    option.series.map(item => {
      item.data = sortedAxis
        .map(sortItem => {
          return (
            mappedData.filter(
              dataItem =>
                (dataItem.legend ? dataItem.legend === item.name : true) &&
                dataItem.axis === sortItem.axis
            )[0] || {}
          )
        })
        .map(sortItem => {
          return Number(String(sortItem?.value).replace(',', ''))
        })
      item.label.formatter = ({ value }) => {
        let formatter = item.label.formatterStr
        try {
          let code = formatter.replace('value', value).match('{(.+?)}')[1]
          let result = runCode(code)
          return formatter.replace(/\{(.+?)\}/, result)
        } catch (error) {
          return value
        }
      }
    })
  }
  return { option }
}
