import { axios } from '@/utils/request'

// get基础信息
export const getBaseInfo = params => {
  return axios({
    method: 'get',
    url: '/api/v1/baseInfo',
    params
  })
}
// request
export const postRequest = data => {
  return axios({
    method: 'post',
    url: '/api/v1/request',
    data
  })
}
