<template>
  <ul class="line-percentage">
    <li
      v-for="(item, index) in percentage"
      :key="index"
      :style="{
        width: `${item}%`,
        backgroundColor: colors[index]
      }"
    >
      <span
        v-if="dataSource.option.label.show"
        :style="{
          ...labelStyle,
          right: index === percentage.length - 1 ? '0' : 'auto'
        }"
      >
        {{ item }}%
      </span>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  computed: {
    percentage() {
      let { data } = this.dataSource
      let total = data
        .map(item => Number(item))
        .reduce((memo, cur) => memo + cur)
      return data.map(item => ((item / total) * 100).toFixed(2))
    },
    colors() {
      return this.dataSource.option.colors
    },
    labelStyle() {
      let { position, fontSize, lineHeight } = this.dataSource.option.label
      return {
        ...this.dataSource.option.label,
        top:
          position === 'bottom'
            ? '100%'
            : position === 'middle'
            ? '50%'
            : undefined,
        bottom: position === 'top' ? '100%' : undefined,
        transform: position === 'middle' ? 'translateY(-50%)' : undefined,
        fontSize: `${fontSize}px`,
        lineHeight: `${lineHeight}px`
      }
    }
  }
}
</script>

<style lang="less" scoped>
.line-percentage {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;

  & > li {
    position: relative;
    top: 50%;
    height: 50%;
    transform: translateY(-50%);

    & > span {
      position: absolute;
    }
  }
}
</style>
