// modules
import { jsonp } from 'vue-jsonp'
// apis
import { postRequest } from '@/api/v1'
// utils
import { axios } from '@/utils/request'
import { randomNum } from '@/utils/methods'

/**
 * 根据类型发送请求
 * @param {String} mode
 * @param {Function} callback
 */
export default ({ api, mode, method, headers }, resolve, reject) => {
  let promise
  api = encodeURI(api)
  switch (mode) {
    case 'straight':
      promise = axios({
        method,
        url: api,
        headers
      })
      break
    case 'forward':
      promise = postRequest({
        api: encodeURI(api), // 再次编码，因为转发时后端会进行一次解码
        headers,
        method
      })
      break
    case 'jsonp':
      const callbackName = `jsonp_${randomNum()}`
      promise = jsonp(api, {
        callbackName
      })
      break
  }
  promise.then(
    res => resolve(res),
    err => reject(err)
  )
  return promise
}
