  <div
    class="img"
    :style="{
      ...dataSource.style,
      backgroundImage: `url('${dataSource.data.url}')`
    }"
  >
    <img
      v-if="!dataSource.data.url"
      :src="placeholderImage"
      alt="placeholderImage"
    />
  </div>
</template>
<script>
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  computed: {
    placeholderImage() {
      return require('./images/thumbnail.png')
    }
  }
}
</script>

