import { sortAxis } from '@/utils/methods'

export default function({ data, option }) {
  const axis = sortAxis(data, option)
  option.xAxis.data = axis.map(sortItem => sortItem.axis)
  // option.yAxis.data = axis.map(sortItem => sortItem.axis)
  option.legend.data = [...new Set(data.map(item => item.legend))]
  option.series.map(item => {
    if (item.type === 'line') {
      item.yAxisIndex = 1
    }
    item.data = axis
      .map(sortItem => {
        return (
          data.filter(
            dataItem =>
              (dataItem.legend ? dataItem.legend === item.name : true) &&
              dataItem.axis === sortItem.axis
          )[0] || {}
        )
      })
      .map(sortItem => Number(String(sortItem?.value).replace(',', '')))
  })
  return { option }
}
