export default function({ data, option }) {
  let legend = option?.fieldMaps?.legend || 'legend'
  let value = option?.fieldMaps?.value || 'value'
  option.legend.data = [...new Set(data.map(item => item[legend]))]
  option.series.map(item => {
    item.data = data.map(dataItem => {
      return {
        value: Number(String(dataItem[value]).replace(',', '')),
        name: dataItem[legend]
      }
    })
  })
  return { option }
}
