<template>
  <div
    class="chart"
    :style="{
      ...dataSource.style
    }"
  ></div>
</template>

<script>
import { runCode } from '@/utils/methods'
import { themeColor } from '@/constants/chart'

export default {
  name: 'Chart',
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      constants: {
        themeColor
      },
      chart: undefined
    }
  },
  computed: {
    option() {
      const { option } = this.dataSource
      const {
        color = [],
        title = {},
        xAxis,
        yAxis,
        series = {},
        legend = {},
        radar
      } = option
      return {
        backgroundColor: 'transparent',
        ...option,
        color,
        title: {
          ...title,
          top: `${title.top}%`,
          left: `${title.left}%`
        },
        xAxis: xAxis
          ? {
              ...xAxis,
              axisLabel: {
                ...xAxis.axisLabel,
                formatter: runCode(xAxis.axisLabel.formatter)
              }
            }
          : undefined,
        yAxis: yAxis
          ? Array.isArray(yAxis)
            ? yAxis.map(item => ({
                ...item,
                axisLabel: {
                  ...item.axisLabel,
                  formatter: runCode(item.axisLabel.formatter)
                }
              }))
            : {
                ...yAxis,
                axisLabel: {
                  ...yAxis.axisLabel,
                  formatter: runCode(yAxis.axisLabel.formatter)
                }
              }
          : undefined,
        series: series.map(item => {
          return {
            ...item,
            radius: item?.radius?.map(
              (radiusItem, radiusIndex) => `${item.radius[radiusIndex]}%` // 兼容占比圆环
            ),
            barCategoryGap: item.barCategoryGap
              ? `${item.barCategoryGap}%`
              : undefined,
            itemStyle: item.itemStyle
              ? {
                  ...item.itemStyle,
                  color: item.itemStyle.color?.colorStops?.length
                    ? new echarts.graphic.LinearGradient(
                        item.itemStyle.color.x,
                        item.itemStyle.color.y,
                        item.itemStyle.color.x2,
                        item.itemStyle.color.y2,
                        item.itemStyle.color?.colorStops
                      )
                    : item.itemStyle.color,
                  borderRadius: series[0].itemStyle?.borderRadius // 兼容占比圆环
                }
              : undefined,
            lineStyle: item.lineStyle
              ? {
                  ...item.lineStyle,
                  color: item.lineStyle.color?.colorStops?.length
                    ? new echarts.graphic.LinearGradient(
                        item.lineStyle.color.x,
                        item.lineStyle.color.y,
                        item.lineStyle.color.x2,
                        item.lineStyle.color.y2,
                        item.lineStyle.color?.colorStops
                      )
                    : item.lineStyle.color
                }
              : undefined,
            areaStyle: item.areaStyle
              ? {
                  ...item.areaStyle,
                  color: item.areaStyle.color?.colorStops?.length
                    ? new echarts.graphic.LinearGradient(
                        item.areaStyle.color.x,
                        item.areaStyle.color.y,
                        item.areaStyle.color.x2,
                        item.areaStyle.color.y2,
                        item.areaStyle.color?.colorStops
                      )
                    : item.areaStyle.color
                }
              : undefined
          }
        }),
        legend: {
          ...legend,
          top: `${legend.top}%`,
          left: `${legend.left}%`,
          formatter: legend.formatter
            ? name => {
                const data = this.dataSource.data
                return runCode(legend.formatter)(name, data)
              }
            : undefined
        },
        radar: radar
          ? {
              ...radar,
              center: radar?.center?.map(centerItem => `${centerItem}%`),
              radius: `${radar?.radius}%`
            }
          : undefined
      }
    }
  },
  watch: {
    'dataSource.layout.width': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.chart.resize()
      }
    },
    'dataSource.layout.height': function(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.chart.resize()
      }
    },
    'dataSource.option': {
      handler: function(newVal, oldVal) {
        // 由于 JSON.stringify 对 function 无效，所以注释掉这层判断
        // if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
        this.setOption(true)
        // }
      },
      deep: true
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart()
    })
  },
  methods: {
    initChart() {
      this.chart = echarts.init(this.$el, 'dark', {
        renderer: 'svg'
      })
      this.setOption()
    },
    setOption(notMerge) {
      this.chart.setOption({ ...this.option }, notMerge)
    }
  }
}
</script>

<style lang="less" scoped>
.chart {
  width: 100%;
  height: 100%;
}

rect {
  fill: transparent !important;
}
</style>
