<template>
  <div class="icon-wrapper" :style="dataSource.style">
    <div
      v-if="dataSource.data.iconImg"
      class="icon-img"
      :style="{
        backgroundImage: `url('${dataSource.data.iconImg}')`
      }"
    ></div>
    <Icon
      v-else
      :type="dataSource.data.iconType || 'smile'"
      :theme="dataSource.data.theme"
      :style="iconStyle"
      :two-tone-color="dataSource.style.color.slice(0, 7)"
    />
  </div>
</template>
<script>
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  computed: {
    iconStyle() {
      const {
        layout: { width, height },
        style: {
          borderWidth,
          paddingTop,
          paddingLeft,
          paddingRight,
          paddingBottom
        }
      } = this.dataSource
      let regexp = /undefined|px/g
      let exactW =
        width -
        Number(paddingLeft.replace(regexp, '')) -
        Number(paddingRight.replace(regexp, ''))
      let exactH =
        height -
        Number(paddingTop.replace(regexp, '')) -
        Number(paddingBottom.replace(regexp, ''))
      const min = Math.min(exactW, exactH)
      const fontSize = min - Number(borderWidth.replace(regexp, '')) * 2
      return {
        fontSize: `${fontSize}px`,
        lineHeight: `${exactH}px`
      }
    }
  }
}
</script>

<style lang="less" scoped>
.icon-wrapper {
  width: 100%;
  height: 100%;
  line-height: 0;
  text-align: center;
  .icon-img {
    width: 100%;
    height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
  }
}
</style>
