import { chinaJSON, chinaCitiesJSON } from '@/constants/geo'

const geoCoordMap = { ...chinaJSON, ...chinaCitiesJSON }

const convertData = function({ data, option: { fieldMaps } }) {
  let res = []
  for (let i = 0; i < data.length; i++) {
    let name = data[i][fieldMaps?.name || 'name'].replace(
      /(省|市|回族|壮族|维吾尔|自治区)/g,
      ''
    )
    let value = Number(data[i][fieldMaps?.value || 'value'])
    let geoCoord = geoCoordMap[name]
    if (geoCoord) {
      res.push({
        name,
        value: geoCoord.concat(value)
      })
    }
  }
  return res
}

export default function({ option, layout }) {
  let visibleLayers = option.layers.filter(item => item.visible)
  for (let item of visibleLayers) {
    const data = convertData(item)
    item.option.series[0].data = data
    item.option.visualMap &&
      option.visualMap.push({
        ...item.option.visualMap,
        seriesIndex: option.series.length,
        pieces: data.map(childItem => childItem.value[2])
      })
    let maxData = Math.max.apply(
      Math,
      item.data.map(mapItem => mapItem.value)
    )
    option.series.push(
      ...item.option.series.map(series => ({
        ...series,
        symbolSize: val =>
          (val[2] / maxData) * layout.width * (series.symbolSize / 100)
      }))
    )
  }

  return { option }
}
