import { axios } from '@/utils/request'
// postDashboardToken
export const postDashboardToken = id => {
  return axios({
    method: 'post',
    url: `/api/v1/dashboard/${id}/token`
  })
}
// getDashboard​ByToken
export const getDashboardByToken = token => {
  return axios({
    method: 'get',
    url: `/api/v1/dashboard/token/${token}`
  })
}
