<template>
  <div
    ref="count"
    class="num-card"
    :style="{
      ...dataSource.style
    }"
  >
    <countTo
      ref="countTo"
      :start-val="startVal"
      :end-val="endVal"
      :duration="option.duration * 1000 || undefined"
      :decimals="option.decimals || 0"
      :separator="option.separator"
    />
    <span
      :style="{
        ...option.unit.style,
        fontSize: option.unit.style.fontSize
          ? `${option.unit.style.fontSize}px`
          : 'inherit'
      }"
    >
      {{ option.unit.text }}
    </span>
    <div class="num__placeholder">{{ endVal }}</div>
  </div>
</template>
<script>
import countTo from 'vue-count-to'
export default {
  name: 'Digitroll',
  components: {
    countTo
  },
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      startVal: 0
    }
  },
  computed: {
    option() {
      return this.dataSource.option
    },
    endVal() {
      let {
        data,
        option: { unit }
      } = this.dataSource
      return String(data).replace(',', '') / (unit.multiple || 1)
    }
  },
  watch: {
    endVal(newVal) {
      this.startVal = newVal
    },
    'option.duration'(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$refs.countTo.start()
      }
    },
    'option.decimals'(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$refs.countTo.start()
      }
    },
    'option.separator'(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$refs.countTo.start()
      }
    }
  }
}
</script>
<style lang="less" scoped>
.num-card {
  white-space: nowrap;
  .num__placeholder {
    opacity: 0;
    height: 0;
  }
}
</style>
