<template>
  <a-spin
    :id="dataSource.id"
    :spinning="loading"
    size="small"
    class="panel"
    :style="{
      width: `${dataSource.layout.width}px`,
      height: `${dataSource.layout.height}px`
    }"
  >
    <template v-if="!loading">
      <component
        :is="componentIs"
        :data-source="generatedDataSource"
        :play="play"
      />
    </template>
    <div
      v-if="
        dataSource.dataSet.type === 'async' &&
          dataSource.dataSet.request &&
          dataSource.dataSet.request.error
      "
      class="panel__request-error"
    >
      <span>
        <a-icon type="warning" />
        {{ dataSource.dataSet.request.error }}
      </span>
    </div>
  </a-spin>
</template>

<script>
// components
import PanelComponents from '@/lib/components'
import componentDataHandlers from '@/lib/components/data-handlers'
// utils
import request from '@/utils/requestByMode'
import {
  deepClone,
  isUndefined,
  evalValue,
  generateBackgroundGradient
} from '@/utils/methods'
export default {
  name: 'Panel',
  components: {
    ...PanelComponents
  },
  props: {
    apiCollection: {
      type: Array,
      required: true,
      default: () => []
    },
    container: {
      type: Object,
      required: true
    },
    dataSource: {
      type: Object,
      required: true
    },
    play: {
      type: Boolean
    }
  },
  data() {
    return {
      loading: true,
      // generatedDataSource 改为 watch 'dataSource' 生成，以便异步处理 dataSource 生成需要的数据
      generatedDataSource: {}
    }
  },
  computed: {
    componentIs() {
      const { type, name } = this.dataSource
      switch (type) {
        case 'chart':
          return 'Chart'
        case 'threeD':
          return 'ThreeD'
        case 'map':
          return 'Map'
        case 'decoration':
          return 'Decoration'
        default:
          const uppercasedName = name
            .split('_')
            .map(item =>
              item.replace(/( |^)[a-z]/g, firstLetter =>
                firstLetter.toUpperCase()
              )
            )
          return uppercasedName.join('')
      }
    }
  },
  watch: {
    dataSource: {
      deep: true,
      handler: function() {
        this.handleData()
      }
    },
    'dataSource.dataSet.start': function(newVal) {
      if (newVal) {
        this.getAsyncData(this.dataSource)
      }
    },
    apiCollection: {
      deep: true,
      handler: function() {
        if (this.dataSource.dataSet.sourceType === 'collection') {
          this.mapApiCollectionData()
        }
      }
    }
  },
  mounted() {
    this.initData()
    this.initAutoSize()
    // 针对 map 组件的图层获取动态数据
    this.initInnerData()
  },
  methods: {
    initAutoSize() {
      this.$nextTick(() => {
        const img = this.$el.querySelector('img')
        if (img) {
          img.onload = () => {
            const { offsetWidth, offsetHeight } = this.$el
            this.setSize(offsetWidth, offsetHeight)
          }
        } else {
          const { offsetWidth, offsetHeight } = this.$el
          this.setSize(offsetWidth, offsetHeight)
        }
      })
    },
    setSize(offsetWidth, offsetHeight) {
      const {
        layout: { x, y, width, height }
      } = this.dataSource
      this.dataSource.layout = {
        ...this.dataSource.layout,
        x: isUndefined(x)
          ? this.container.layout.width / 2 - offsetWidth / 2
          : x,
        y: isUndefined(y)
          ? this.container.layout.height / 2 - offsetHeight / 2
          : y,
        width: width || offsetWidth + 4,
        height: height || offsetHeight + 2
      }
    },
    initData() {
      const { api, type, sourceType, interval } = this.dataSource.dataSet
      if (type === 'async') {
        if (sourceType === 'api' && api) {
          this.getAsyncData(this.dataSource)
          if (this.play) {
            this.intervalGetAsyncData(interval, this.dataSource.id, () =>
              this.getAsyncData(this.dataSource)
            )
          }
        } else if (sourceType === 'collection') {
          this.mapApiCollectionData()
        }
      } else {
        this.handleData()
      }
    },
    initInnerData() {
      for (let item of this.dataSource?.option?.layers || []) {
        const { api, type, interval } = item.dataSet
        if (type === 'async' && api) {
          this.getAsyncData(item)
          if (this.play) {
            this.intervalGetAsyncData(interval, item.id, () =>
              this.getAsyncData(item)
            )
          }
        }
      }
    },
    mapApiCollectionData() {
      const { key, id } = this.dataSource.dataSet.collection || {}
      const data = this.apiCollection.find(item => item.api === id)?.data
      this.$set(this.dataSource, 'data', evalValue(key, data))
    },
    getAsyncData(source) {
      let { api } = source.dataSet
      this.$set(source.dataSet, 'request', {})
      if (api) {
        request(
          source.dataSet,
          res => {
            source.data = deepClone(res.data === undefined ? res : res.data)
            this.$set(source.dataSet, 'start', false)
          },
          err => {
            source.dataSet.request = { err: 'Network Error' }

            // // 接口报错，会对秒级轮询进行降维操作改成分级轮询
            // clearInterval(this[source.id])
            this.$set(source.dataSet, 'start', false)
          }
        )
      }
    },
    intervalGetAsyncData(interval, timer, fn) {
      clearInterval(this[timer])
      if (Number(interval)) {
        this[timer] = setInterval(fn, interval * 1000)
      }
    },
    handleData() {
      const {
        backgroundImage,
        backgroundGradient,
        padding,
        paddingTop,
        paddingLeft,
        paddingRight,
        paddingBottom,
        borderWidth,
        borderRadius,
        boxShadowH,
        boxShadowV,
        boxShadowBlur,
        boxShadowSpread,
        boxShadowColor,
        boxShadowInset,
        fontSize,
        textIndent,
        letterSpacing,
        lineHeight,
        rotate
      } = this.dataSource.style

      let handler = componentDataHandlers[this.dataSource.name]

      let style = {
        borderStyle: 'solid',
        ...this.dataSource.style,
        backgroundImage: backgroundImage
          ? `url("${backgroundImage}")`
          : generateBackgroundGradient(backgroundGradient),
        backgroundPosition: 'center center',
        padding: `${padding}px`,
        paddingTop: `${paddingTop}px`,
        paddingLeft: `${paddingLeft}px`,
        paddingRight: `${paddingRight}px`,
        paddingBottom: `${paddingBottom}px`,
        borderWidth: `${borderWidth || 0}px`,
        borderRadius: `${borderRadius}px`,
        boxShadow: `${boxShadowH || 0}px
          ${boxShadowV || 0}px
          ${boxShadowBlur || 0}px
          ${boxShadowSpread || 0}px
          ${boxShadowColor}
          ${boxShadowInset === 'inset' ? 'inset' : ''}`,
        fontSize: `${fontSize}px`,
        textIndent: `${textIndent}px`,
        letterSpacing: `${letterSpacing}px`,
        lineHeight: `${lineHeight}px`,
        transform: (rotate || rotate === 0) && `rotate(${rotate}deg)`
      }
      if (typeof handler === 'function') {
        // handler 可能是同步也可能是异步
        Promise.resolve(handler(deepClone(this.dataSource)))
          .then(({ data, option }) => {
            this.generatedDataSource = {
              ...this.dataSource,
              data: data || this.dataSource.data,
              option: option || this.dataSource.option,
              style
            }
            this.loading = false
          })
          .catch(e => {
            throw e
          })
      } else {
        this.generatedDataSource = {
          ...this.dataSource,
          style
        }
        this.loading = false
      }
    }
  }
}
</script>

<style lang="less" scoped>
.panel {
  position: relative;
  width: 100%;
  height: 100%;

  /deep/.ant-spin-container {
    width: 100%;
    height: 100%;
  }

  /deep/.ant-spin-dot {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -7px 0 -7px 0;
  }

  .panel__request-error {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #1c2026;
    box-shadow: 0 0 0 2px #f5222d;
    font-size: 12px;
    white-space: nowrap;
    overflow: hidden;
    color: #f5222d;
  }
}
</style>
