import { axios } from '@/utils/request'

// getDashboard​
export const getDashboardById = id => {
  return axios({
    method: 'get',
    url: `/api/v1/dashboard/${id}`
  })
}
// deleteDashboard​
export const deleteDashboardById = id => {
  return axios({
    method: 'delete',
    url: `/api/v1/dashboard/${id}`
  })
}
// updateDashboardById​
export const putDashboardById = data => {
  return axios({
    method: 'put',
    url: `/api/v1/dashboard/${data.id}`,
    data
  })
}
