<template>
  <a-table
    :columns="columns"
    :data-source="tableData"
    :pagination="false"
    :style="{
      ...dataSource.style
    }"
    :row-key="(record, index) => index + 1"
    :show-header="option.header.show === false ? false : true"
    :transform-cell-text="transformCellText"
    size="small"
    bordered
  >
    <template slot="sequence" slot-scope="text, record, index">
      <template v-if="['number', 'roman', 'cn'].indexOf(sequence.type) !== -1">
        {{ formatNumber(index + 1, sequence.type) }}
      </template>
      <Icon v-else-if="sequence.type === 'check'" :type="sequence.type" />
      <span
        v-else
        :class="sequence.type"
        :style="{
          width: dataSource.style.fontSize,
          height: dataSource.style.fontSize,
          backgroundColor: dataSource.style.color
        }"
      ></span>
    </template>
    <template slot="text" slot-scope="text">
      {{ text }}
    </template>
    <template slot="img" slot-scope="text">
      <img :src="text" alt="" />
    </template>
  </a-table>
</template>
<script>
import { formatNumber, runCode } from '@/utils/methods'
export default {
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  computed: {
    option() {
      return this.dataSource.option
    },
    sequence() {
      return this.option.sequence
    },
    columns() {
      const { sequence, columns } = this.option
      const rawData = [...columns].map(item => ({
        ...item,
        scopedSlots: { customRender: item.contentType }
      }))
      if (sequence.show) {
        rawData.unshift({
          name: '序号',
          scopedSlots: { customRender: 'sequence' }
        })
      }
      const newData = rawData.map(({ key, label, width, scopedSlots }) => {
        const style = this.option.header?.style
        return {
          title: label,
          key,
          dataIndex: key,
          width: `${width}%`,
          customHeaderCell: () => {
            return {
              style: {
                ...this.dataSource.style,
                ...style,
                fontSize: `${style?.fontSize}px`,
                textIndent: `${style?.textIndent}px`,
                letterSpacing: `${style?.letterSpacing}px`,
                lineHeight: `${style?.lineHeight}px`
              }
            }
          },
          customCell: (item, index) => {
            return {
              style: {
                ...this.dataSource.style,
                backgroundImage: 'none'
              }
            }
          },
          scopedSlots
        }
      })
      return newData
    },
    tableData() {
      return [...this.dataSource.data]
    }
  },
  watch: {
    'option.carousel': {
      handler: function(newVal, oldVal) {
        this.animate()
      },
      deep: true
    }
  },
  mounted() {
    this.animate()
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  methods: {
    formatNumber,
    animate() {
      clearInterval(this.timer)
      // const tbody = this.$el.querySelector('tbody')
      // tbody.style.transform = 'translateY(0px)'
      if (this.option?.carousel?.type === 'single' && this.tableData.length) {
        setTimeout(() => {
          this.singleScroll()
        }, 1000)
      }
    },
    singleScroll() {
      clearInterval(this.timer)
      // const tbody = this.$el.querySelector('tbody')
      // const tr = tbody.querySelector('tr')
      // let offset = 0
      this.timer = setInterval(() => {
        // tbody.style.transform = `translateY(${offset}px)`
        // offset -= tr.offsetHeight
        // if (offset < -tbody.offsetHeight) {
        //   offset = 0
        // }
        this.tableData.push(this.tableData.shift())
        this.$forceUpdate()
      }, (this.option.carousel.speed || 1) * 1000)
    },
    transformCellText({ text, column, record }) {
      try {
        let formatter = this.dataSource.option.columns.filter(
          item => item.key === column.key
        )[0].formatter
        const value = record[column.key]
        let code = formatter.replace('value', value).match('{(.+?)}')[1]
        const newValue = runCode(code)
        if (newValue) {
          return formatter.replace(/\{(.+?)\}/, newValue)
        } else {
          return text
        }
      } catch (error) {
        return text
      }
    }
  }
}
</script>

