.ant-table-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border: 1px solid #1f1f1f;
  /deep/.ant-spin-nested-loading {
    height: 100%;
  }
  /deep/.ant-table {
    height: 100%;
    border: none;
    .ant-table-content {
      height: 100%;
      border: none;
      .ant-table-body {
        margin: 0;

        table {
          tbody {
            position: relative;
            z-index: -1;
