          }
          th,
          td {
            border-top: none !important;
            border-left: none !important;

            img {
              display: block;
              width: 100%;
            }
          }
          th:last-child,
          td:last-child {
            border-right: none !important;
          }
          tr:last-child {
            td {
              border-bottom: none !important;
            }
          }
        }
      }
      .ant-table-placeholder {
        position: relative;
        height: 100%;
        background: transparent;
        .ant-empty {
          position: absolute;
          top: 25%;
          left: 50%;
        }
      }
    }
  }
}
.dot,
.square {
  display: block;
  transform: scale(0.5);
}
