<template>
  <div
    class="decoration"
    :style="{
      ...dataSource.style
    }"
  >
    <div v-if="dataSource.option.lottie" ref="lottie"></div>
    <img v-else :src="dataSource.option.img" />
  </div>
</template>

<script>
import lottie from 'lottie-web'
export default {
  name: 'Chart',
  props: {
    dataSource: {
      type: Object,
      required: true
    }
  },
  mounted() {
    const {
      option: { lottie }
    } = this.dataSource
    if (lottie) {
      this.initLottie(lottie)
    }
  },
  methods: {
    initLottie(lottieName) {
      lottie.loadAnimation({
        container: this.$refs.lottie,
        renderer: 'svg',
        loop: true,
        autoplay: true,
        path: `${location.origin}/lotties/${lottieName}/data.json`
      })
    }
  }
}
</script>

<style lang="less" scoped>
.decoration {
  width: 100%;
  height: 100%;
  & > img {
    width: 100%;
    height: 100%;
  }
}
</style>
