import { runCode } from '@/utils/methods'

export default function({ data = [], option = {} }) {
  if (Array.isArray(data)) {
    let mappedData = data?.map(item => {
      let legend = option?.fieldMaps?.legend || 'legend'
      let axis = option?.fieldMaps?.axis || 'axis'
      let value = option?.fieldMaps?.value || 'value'
      return {
        legend: String(item[legend]),
        axis: item[axis],
        value: item[value]
      }
    })
    option.legend.data = [...new Set(mappedData.map(item => item.legend))]
    option.radar.indicator = [
      ...new Set(mappedData.map(item => item.axis))
    ].map(item => ({ name: item }))
    option.series.map(item => {
      item.data = option.legend.data.map((legendItem, legendIndex) => {
        const origin = item.data[legendIndex]
        return {
          ...origin,
          name: legendItem,
          value: mappedData
            .filter(filterItem => filterItem.legend === legendItem)
            .map(item => item.value),
          label: {
            ...origin.label,
            formatter: ({ value }) => {
              let formatter = origin.label.formatterStr
              try {
                let code = formatter.replace('value', value).match('{(.+?)}')[1]
                let result = runCode(code)
                return formatter.replace(/\{(.+?)\}/, result)
              } catch (error) {
                return value
              }
            }
          }
        }
      })
    })
  }
  return { option }
}
